@import url('https://fonts.googleapis.com/css2?family=Montserrat:wght@700&display=swap');

html, body {
  background-color: #FCF5F6;
  font-family: 'Montserrat', sans-serif;
}

.content-position {
  margin: 0;
  padding : 25px 150px;
}

.title-content-position {
  margin: 0;
  padding : 150px 150px;
}

.container-all {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: center;
}

#container-navbar {
  top: 0;
  list-style-type: none;
  overflow: hidden;
  background-color: #333;
  font-size: 20px;
}

.navbar-left li, .left {
  float: left;
}

.navbar-right li, .right {
  float: right;
}

.navbar-right li a {
  display: block;
  color: #FCF5F6;
  text-align: center;
  padding: 14px 16px;
  text-decoration: none;
}

.navbar-right li a:hover {
  border-bottom: solid 3px #FFD700;
  transition: transform 250ms ease-in-out;
}
.navbar-right li a:hover:after { 
  transform: scaleX(1); 
}
.navbar-right li a.fromRight:after{
  transform-origin: 100% 50%; 
}
.navbar-right li a.fromLeft:after{  
  transform-origin:  0% 50%; 
}

.content {
  color: #333;
  background-image: url(../public/assets/wave.svg);
  background-attachment: fixed;
  background-repeat: no-repeat;
}

.content-title {
  font-size: 72px;
  margin: 0px;
}

.title {
  margin-bottom: 50px;
}

.btn-go {
  display: inline-block;
  padding: 0.5em 1.7em;
  margin: 0 0.1em 0.1em 0;
  border: 3px solid #333;
  background-color: #FCF5F6;
  border-radius: 2em;
  box-sizing: border-box;
  text-decoration: none;
  font-family: "Montserrat", sans-serif;
  font-weight: 300;
  font-size: 20px;
  color: #333;
  text-shadow: 0 0.04em 0.04em rgba(255, 255, 255, 0.253);
  text-align: center;
  transition: all 0.2s;
  cursor: pointer;
}

.btn-go:hover {
  color: #333;
  background-color: #FFD700;
}

.btn-content {
  display: inline-block;
  padding: 0.6em 1.7em;
  border: 0.1em solid #FCF5F6;
  background-color: transparent;
  margin: 20px;
  border-radius: 0.12em;
  box-sizing: border-box;
  text-decoration: none;
  border-radius: 10px;
  font-weight: 300;
  font-size: 20px;
  color: #FCF5F6;
  text-shadow: 2px 2px 4px #333;
  text-align: center;
  transition: all 0.2s;
}

.btn-content:hover {
  color: #333;
  background-color: #FCF5F6;
}

.subcontent {
  background-color: #202020;
}

.container-split {
  background: url(../public/assets/splitimg.jpg), #FCF5F6;
  background-size: 50%;
  background-position: right bottom, left top;
  background-repeat: no-repeat, no-repeat;
  color: #333;
}

.content-split {
  width: 45%;
  word-wrap: break-word;
}

.text-content-light {
  color: rgba(51, 51, 51, 0.75);
}


.text-content-dark {
  color: #f4f4f4e1;
  text-shadow: 2px 2px 4px #333;
}

.title-dark {
  color: #FCF5F6 !important;
}

.warn {
  color: #FFD700 !important;
}

#add-content {
  text-align: center;
  color: #FCF5F6;
  background: url(../public/assets/bgimg.jpg);
  background-position: center;
  background-repeat: no-repeat;
  background-origin: content-box;
}

.glass-bg {
  padding: 50px 100px;
  height: auto;
  background-color: rgba(255, 255, 255, 0.06);
  border-radius: 20px;
  backdrop-filter: blur(5px);
  margin: 20px 0px;
}

hr {
  margin: 0;
  padding: 0;
  height: 1px;
  background-color: #FFD700;
}

footer {
  text-align: center;
  justify-content: center;
  align-content: center;
  align-items: center;
  height: 10px;
  color: #333;
  font-size: 14px;
  background-color: #FFD700;
}
